import React, { useContext } from 'react';
import {useHistory} from "react-router-dom";

import {UsuarioDataContext} from 'context/usuarioContext';
import {TidDataContext} from 'context/tidContext';
import alertaContext from 'context/alertas/alertaContext';
import {IS_ANDROID, IS_IOS, fromApp} from 'components/constants';

import tidImg from 'img/tid.svg';
import importarImg from 'img/importar.svg';
import acercaImg from 'img/acerca.svg';
import archivoImg from 'img/archivo.svg';
import ajustesImg from 'img/ajustes.svg';
import reporteImg from 'img/reporte.svg';
import ayudaImg from 'img/ayuda.svg';
import consulta_docImg from 'img/consulta_doc.svg';
import ies21 from 'img/logo-ies.svg';

import './menu.css';

export default function Menu({setOpenMenu}) {

    let history = useHistory();

    const { mostrarAlerta } = useContext(alertaContext);

    const isTid = history.location.pathname.indexOf('/tid') > -1;
    
    const {dataUsuario, logoutAction} = useContext(UsuarioDataContext);
    const {dataTid, sesionTid, importarSesion} = useContext(TidDataContext);

    const {idn} = sesionTid && isTid ? sesionTid : 0;

    const getVisto = id => {
        if(!sesionTid.avanceNodos["id"+id]){
            return false;
        }else{
            return true;
        }
    }

    const isActive = path => history.location.pathname.indexOf(path) > -1;

    const importarSesionHandler = () => {
        setOpenMenu(false);
        const text = `¿Está seguro que desea sobreescribir la sesión actual por la de <strong>${dataTid.importa_ses_obj.device}</strong>?<br />No podrá recuperar el avance actual.`;
        mostrarAlerta(text, 'Importar sesión', importarSesion);
        //setDialogProps({...dialogProps, title: 'Importar sesión', text, actionBtn1:importarSesion, isConfirm:true });
        //setOpenDialog(true);
    }

    const logOut = () => {
        setOpenMenu(false);
        const text = '¿Está seguro que desea cerrar la sesión actual?';
        mostrarAlerta(text, 'Cerrar sesión', logoutAction);
        //setDialogProps({...dialogProps, title: 'Importar sesión', text, actionBtn1:logoutAction, isConfirm:true });
        //setOpenDialog(true);
    }

    const go = page => {
        setOpenMenu(false);        
        history.push(`/tid/${dataTid.TID}/${page}`);
    }

    const goAjustes = () => {
        setOpenMenu(false);
        history.push('/ajustes');
    }

    const iraNodo = id => {
        setOpenMenu(false);
        history.push(`/tid/${dataTid.TID}/page/${id}`);
    }

    const descargaPDF = () => {
        setOpenMenu(false);
        window.open(`${dataTid.url_tid}me/materia.pdf`, '_blank');        
    }

    const openAyuda = () => {
        setOpenMenu(false);
        window.open('https://www.ies21.edu.ar/tid6/ayuda/', '_blank');
    }

    const openApp = () => {
        setOpenMenu(false);
        if (IS_IOS && fromApp) {
            window.location = "comies21util://open?page=materias";
            setTimeout(function() {
                if (!document.webkitHidden) {
                    window.location = 'http://itunes.apple.com/app/id1262103263';
                }
            }, 25);
        } else if (IS_ANDROID && fromApp) {
            window.location = 'intent://com.ies21.util/#Intent;scheme=launch;package=com.ies21.util;S.content=materias;end';
        }else{
            history.push('/');
        }
        
    }

    return(        
        <React.Fragment>
        
            <div id="panelizq">
                <div style={{height:'100%',overflowY: 'auto'}}>
                    <div className="headpi" onClick={() => setOpenMenu(false)}>

                        {isTid ? 
                            <img src={tidImg} alt="" />
                        : 
                            <img src={ies21} alt="" />
                        }
                        <br />
                        <p>{isTid ? <>{dataTid.materia}<br /></> : null}



                        <strong>{dataUsuario.nombres} {dataUsuario.apellidos}</strong></p>  
                        

                    </div>

                    <div className="wfijo85">
                    <table>
                    <tbody>

                        {isTid && 
                        <>
                        {dataTid.importa_ses_obj.id ? 
                        <tr onClick={importarSesionHandler} className="bo">
                            <td><img src={importarImg} alt="" /></td><td>Importar avance<br /><span className="f10">Última modificación: {dataTid.importa_ses_obj.fecha} de {dataTid.importa_ses_obj.device}</span></td>
                        </tr> : null }
                        <tr onClick={() => go('editorial')} className={isActive('editorial') ? 'bo active' : 'bo'}>
                            <td><img src={acercaImg} alt="" /></td><td>Editorial</td>
                        </tr>
                        <tr className="nocursor">
                            <td colSpan="2" className="sim timen" style={{height: '40px', backgroundColor: '#EEEEEE'}}><strong>CONTENIDO</strong></td>
                        </tr>
                        <tr>
                            <td colSpan="2" className="sim nopad">
                            <div className="pandercont wfijo85">
                                <ul className="indice">

                                { dataTid.indice.map( ({id, t, n}, index) => {
                                        const classNam1 = (id === idn) ? 'activo' : '';
                                        const classNam2 = 'indice_n' + n;
                                        const classNam3 = getVisto(id) ? 'visto' : '';
                                        return (
                                            <li key={index} className={`${classNam1} ${classNam2} ${classNam3}`} onClick={() => iraNodo(id)}>{t}</li>
                                        );
                                    })
                                }

                                </ul>
                                </div>
                            </td>
                        </tr>
                        <tr onClick={descargaPDF} className="bo">
                            <td><img src={archivoImg} alt="" style={{height:'16px'}} /></td><td>Descargar PDF</td>
                        </tr>

                        <tr onClick={() => go('errores')} className={isActive('errores') ? 'bo active' : 'bo'}>
                            <td><img src={reporteImg} alt="" /></td><td>Reportar problema</td>
                        </tr>

                        {dataTid.consulta_doc &&
                        <tr onClick={() => go('consulta')} className={isActive('consulta') ? 'bo active' : 'bo'}>
                            <td><img src={consulta_docImg} alt="" /></td><td>Consulta al tutor</td>
                        </tr>
                        }
                        </> }

                        <tr onClick={openAyuda} className="bo">
                            <td><img src={ayudaImg} alt="" /></td><td>Ayuda</td>
                        </tr>

                        <tr onClick={goAjustes} className={isActive('ajustes') ? 'bo active' : 'bo'}>
                            <td><img src={ajustesImg} alt="" style={{height:'16px'}} /></td><td>Ajustes</td>
                        </tr>

                        <tr onClick={openApp} className={history.location.pathname === '/' ? 'bo active' : 'bo'}>
                            <td colSpan="2" className="sim">Mis materias</td>
                        </tr>

                        <tr className="bo">                            
                            <td colSpan="2" className="sim nopad">
                                <a href={`https://alumnos.ies21.edu.ar/autlgnapp.asp?t=${dataUsuario.tokenSistemas}&pagina=tids`} target="_blank" rel="noopener noreferrer">Autogestión</a>
                            </td>                            
                        </tr>

                        <tr className="bo">
                            <td colSpan="2" className="sim gRojo" onClick={logOut}>Cerrar sesión</td>
                        </tr>

                        </tbody>           
                    </table>
                    </div>
                    
                </div>
            </div>
        </React.Fragment>
    )
};