import React, {useState, useRef, useEffect} from 'react';

import {getOffsetTop} from 'components/constants';

import './header.css';
import TidLogo from 'img/tid.svg';
import ies21 from 'img/logo-ies.svg';

import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';
import SubLoading from 'components/loading';


const searchWord = {
	findAndReplaceDOMText: window.findAndReplaceDOMText,
	instance: null
}

const Header = React.forwardRef((props, ref) => {
	const {
			titulo, 
			utilesVisible, 
			paginaRef, 
			loading, 
			setOpenMenu, 
			openMenu, 
			imagesLoaded, 
			isTid,
			showUtils, 
			setOpenPanelUtils, 
			openUtils, 
			showPreguntas, 
			setOpenPanelPreguntas, 
			openPreguntas,
			sendAction
		} = props;
  
	const [visibleSearch, setvisibleSearch] = useState(false);

	const refSearch = useRef();
	const htmlPageMemo = useRef();



	const searchText = txt => {

		if(loading) return;

		searchWord.instance && searchWord.instance.revert();

		if(txt.length > 0 ){
			searchWord.instance = searchWord.findAndReplaceDOMText(document.getElementById('sc'), {
				find: txt,
				wrap: 'span',
				wrapClass: 'resultSearch'
			});

			console.log("Results search: " + searchWord.instance.reverts.length);

			if(document.getElementsByClassName("resultSearch")[0] !== undefined){
				const elem = document.getElementsByClassName("resultSearch")[0];
				window.scrollTo({
					top: getOffsetTop(elem) - 52 - 13,
					behavior: 'smooth',
				});
			}
		}
	}

	const showSearch = paginaRef !== undefined ? true : false;
	const showSend = sendAction !== undefined ? true : false;

	useEffect(()=>{
		if(loading || paginaRef === undefined) return;

		if(visibleSearch){			
			refSearch.current.focus();
		}
		// eslint-disable-next-line
	}, [refSearch, loading, visibleSearch]);

	useEffect(() => {
		if(!imagesLoaded || loading){
			setvisibleSearch(false);
		}
	}, [imagesLoaded, loading]);

	useEffect(() => {
		if(utilesVisible && visibleSearch){
			searchWord.instance && searchWord.instance.revert();
			setvisibleSearch(false)
		}
	}, [utilesVisible, visibleSearch])


	const handlerSearch = (open) => {		
		if(!open){
			searchText('');
		}else{
			htmlPageMemo.current = paginaRef.current.innerHTML;
		}
		setvisibleSearch(open)
	}


	const trigger = useScrollTrigger();

	const getPaddingH1 = () => {
		if(showPreguntas && showUtils){
			return '140px';
		}else if(showPreguntas || showUtils){
			return '90px';
		}else{
			return '60px';
		}
	}

	const getClassSearch = (uv, vs) => {
		let classe = 'buscar-btn';
		if(vs) classe = 'buscar-btn close';
		if(uv) classe = 'buscar-btn buscar-disabled';
		return classe;
	}


	return (
		<React.Fragment>
			<Slide in={(visibleSearch || loading) ? true : !trigger}>
				<header className="conten cHom" ref={ref}>
					{!loading ? 
					<div className="logo_menu tid-logo" onClick={() => setOpenMenu(!openMenu)}>
						{isTid ?
						<img src={TidLogo} alt="" />
						:
						<img src={ies21 } className="ieslogo" alt="" />
						}
					</div>
					:
					<div className="logo_menu tid-logo disabled">
						{isTid ?
						<img src={TidLogo} alt="" className="disabled" />
						:
						<img src={ies21 } className="ieslogo disabled" alt="" />
						}
					</div>
					}
					<h1 style={{paddingRight:getPaddingH1()}}>{loading && <SubLoading /> }{titulo}</h1>
					{ !loading && 
						<React.Fragment>				
							{ showSearch &&
								<div className="buttonstop">	
									{ showUtils && !visibleSearch ? <div id="menu"  onClick={() => setOpenPanelUtils(!openUtils)}></div> : null }
									{ showPreguntas && !visibleSearch ? <div id="preguntas"  onClick={() => setOpenPanelPreguntas(!openPreguntas)}></div> : null }
									<Slide direction="left" in={visibleSearch} mountOnEnter unmountOnExit>
										<input type="text" className="search" ref={refSearch} onChange={e => searchText(e.target.value)} id="busqueda" placeholder="Buscá en esta página" />
									</Slide>
									<div className={getClassSearch(utilesVisible ,visibleSearch)} onClick={() => handlerSearch(!visibleSearch)}></div>
								</div>
							}

							{ showSend && <button className="enviarh" onClick={sendAction}>Enviar</button> }

							
						</React.Fragment>
					}
				</header>
			</Slide>
		</React.Fragment>
	);
});
export default React.memo(Header);