import React from 'react';

import './utiles.css';

export default function Utiles({noteSelectedText, resaltarSelectedText, unResaltarSelectedText, propsUtiles, setpropsUtiles}){

    if(!propsUtiles.visible) return null;

    const onclickutil = action => {
        setpropsUtiles({...propsUtiles , visible:false});
        action();
    }


    return(
        <div id="indicador" style={{position: propsUtiles.position, right: propsUtiles.right, top:propsUtiles.top, left:propsUtiles.left}}>
            <button id="anotar" title="agregar nota"  onClick={() => onclickutil(noteSelectedText)}></button>
            <button id="pintar" title="marcar selección"  onClick={() => onclickutil(resaltarSelectedText)}></button>
            <button id="despintar" title="desmarcar"  onClick={() => onclickutil(unResaltarSelectedText)}></button>
        </div>
    )
}