import React from 'react';

import AlertaState from './context/alertas/alertaState';
import PushState from './context/notificacionespush/pushState';
import { SnackbarProvider } from 'notistack';

import App from './App';

export default function MainApp() {

    return(
        <SnackbarProvider 
			autoHideDuration={3000}
			anchorOrigin={{
				vertical: 'top',
				horizontal: 'center',
			}}
		>
            <AlertaState>
                <PushState>
                    <App />
                </PushState>
            </AlertaState>
        </SnackbarProvider>        
    )
    
}