import React, { useEffect, useState, useContext } from 'react';
// import { Link } from "react-router-dom";
import Skeleton from 'react-loading-skeleton';
import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';
import useLocalStorage from 'hooks/useLocalStorage';
import {VERAPP} from 'components/constants';

import axios from 'axios';
import Carrera from 'components/carrera/carrera';
import Header from 'components/header/header';
import Avatar from 'components/avatar';

// import tid_btn from 'img/tid-btn.svg';

function Home({dni, openMenu, setOpenMenu}) {

    const {dataUsuario, remoteConstants} = useContext(UsuarioDataContext);
    const { mostrarAlerta } = useContext(alertaContext);

    const [dataCarreras, setDataCarreras] = useState(null);
    const [loading, setLoading] = useState(true);   

    //const [lastAccessTids, setLasAccessTids] = useState([]);

    const [uuidDevice] = useLocalStorage('uuidDevice');


    useEffect(() => { 
        if(dni && !dataCarreras){
            const fetchTids = async () => {

                setLoading(true);

                const data = { dni, VERAPP, uuidDevice };

                try {
                    const result = await axios.post(`${remoteConstants.API_IES}get_tids.php`, data);

                    console.log(result);               
        
                    if(result.data.r === 0){
                        mostrarAlerta(result.data.text, "Error");
                        setLoading(false);
                        return false;
                    }
                    
                    console.log(result.data.carreras)
                    if(result.data.carreras){
                        setDataCarreras({
                                alumno: result.data.carreras,
                                moderador: result.data.carrerasModerador,
                            });
                    }
        
                }catch (error) {
                    mostrarAlerta(error, "Error");
                }
                setLoading(false);
            }
            fetchTids();
        }
        // eslint-disable-next-line
    }, [dni]);

    if(!dataUsuario) return null;
    
    return (
        <div className="home">

            <Header setOpenMenu={setOpenMenu} openMenu={openMenu} loading={loading} 
                titulo={!loading ? <strong style={{display: 'inline-flex'}}>
                    <Avatar dataUsuario={dataUsuario} />
                    <span style={{marginLeft: '7px'}}>{dataUsuario.nombres} {dataUsuario.apellidos}</span></strong> : 'Cargando datos...'} 
            />

            <div className="section flex nosombra" style={{paddingLeft:0, paddingRight:0}}> 

            { (dni && dataCarreras) ?
                <>
                    { dataCarreras.moderador.length > 0 ?
                    <>
                        <h4 style={{color: '#3B9BDC', padding: '0 15px'}}>Carreras docente</h4>
                        <div className="carreras">
                            {   dataCarreras.moderador.map( carrera => {                    
                                    return (
                                        <Carrera key={carrera.id} data={carrera} />
                                    )
                                })
                            }
                        </div>
                        <hr />
                    </>
                    : null }

                    { dataCarreras.alumno.length > 0 ?

                    <>
                    { dataCarreras.moderador.length > 0 ? <h4 style={{color: '#3B9BDC', padding: '0 15px'}}>Carreras alumno</h4> : null}

                    <div className="carreras">
                        {   dataCarreras.alumno.map( carrera => {                    
                                return (
                                    <Carrera key={carrera.id} data={carrera} />
                                )
                            })
                        }
                    </div>
                    </>

                    : null }
                    
                </>
            :

                <div className="carreras">
                    <h1><Skeleton /></h1>

                    <ul className="anios">
                        <li className="desplegado">
                            <div className="li"><Skeleton /></div>
                            <ul className="materia">
                                <li><Skeleton /></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                            </ul>
                        </li>
                    </ul>

                    <ul className="anios">
                        <li className="desplegado">
                            <div className="li"><Skeleton /></div>
                            <ul className="materia">
                                <li><Skeleton /></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                            </ul>
                        </li>
                    </ul>

                </div>
            }
            </div>
        </div>
    );
}

export default React.memo(Home);