import React, { useState, useEffect, useContext, useRef } from 'react';
import { useParams, useHistory } from "react-router-dom";
import axios from 'axios';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Skeleton from 'react-loading-skeleton';

import PreguntasState from 'context/preguntas/preguntasState';

import {TidDataContext} from 'context/tidContext';
import alertaContext from 'context/alertas/alertaContext';

import {API_BASE, setImages, winSize, get_urlTid, IS_MOBILE} from 'components/constants';

import useUtiles from 'hooks/useUtiles';
import useLocalStorage from 'hooks/useLocalStorage';

import Media from 'components/tid/media';
import Scroll from 'components/tid/scroll/scroll';
import Footer from 'components/tid/footer/footer';
import Header from 'components/header/header';
import Utiles from 'components/tid/utiles/utiles';
import Panel from 'components/tid/panel/panel';
import PanelPreguntas from 'components/tid/panel/panelPreguntas';
import { useCallback } from 'react';

function Page({openMenu, setOpenMenu}) {

    let history = useHistory();

    const {TID, idn} = useParams();   
    
    const {dataTid, sesionTid, setTID, setAvance, guardaSesion, getNodoData} = useContext(TidDataContext);
    const { mostrarAlerta } = useContext(alertaContext);

    const paginaRef = useRef(null);

    //const [htmlPage, sethtmlPage] = useState('');

    const [open, setOpen] = useState(false);
    const [modalData, setModalData] = useState({});
    const [loading, setLoading] = useState(true);
    const [loadingTxt, setLoadinTxt] = useState('Cargando datos...');
    const [propsUtiles, setpropsUtiles] = useState({});

    const {updateUtiles, clearNode, resaltarSelectedText, unResaltarSelectedText, noteSelectedText, viewNote, panelresums ,panelnots, eliminarNotas, eliminarResumenes, NoteNode} = useUtiles({sesionTid, paginaRef, guardaSesion});

    const minWinHeight = winSize().height;
    const URL_TID = get_urlTid(TID);

    const handleClose = () => {
        setOpen(false);
        setModalData({});
    }


    const [imagesLoaded, setImagesLoaded] = useState(false);

    const onloadImg = e => {
        // console.log(e.target.naturalWidth);
        e.target.className = e.target.className.replace(' loadingimg', '');
        e.target.removeAttribute("style");
    }

    const onImagesLoaded = useCallback( () => {
               
        const images_im = paginaRef.current.getElementsByTagName("img");

        for (let index = 0; index < images_im.length; index++) {
            const element = images_im[index];
            element.className = element.className + ' loadingimg';
            element.style = 'width:'+element.width+'px;height:'+element.height+'px; display:inline-block';
            element.onload = onloadImg;
            
            //element.style = 'background-color:#ccc'
            //element.addEventListener('onload', onloadImg);
            
        }

        const total = images_im.length;
        let loaded = 0;
        if(total > 0){
            const interval = setInterval(() => {
                setLoadinTxt(`Cargando imagen ${(loaded+1)} de ${total}...`);
                for(let i = 0; i < total; i++){
                    const el = images_im.item(i);
                    if(el.naturalWidth !== 0){
                        loaded++;
                        //console.log("loaded", el.src, loaded, "of", total);
                    }
                }
                if(loaded >= total){
                    setImagesLoaded(true);
                    clearTimeout(interval);
                }
            }, 500);
        } else{
            setImagesLoaded(true);
        }
    }, []);

    useEffect(() => {
        if(imagesLoaded !== false){
            setLoading(false);
            setLoadinTxt('Cargando datos...');
            setImages(paginaRef.current);
            setAvance();            
            updateUtiles();
        }
        // eslint-disable-next-line
    },[imagesLoaded])

    const onEndSelect = e => {
        if(dataTid && !loading) {
            const sel = window.getSelection().toString().trim();
            if(sel !== '' && e.isTrusted){
                let left = e.pageX  ? e.pageX  : e.changedTouches[0].pageX ;
                let top = e.pageY ? e.pageY : e.changedTouches[0].pageY;
                top = Math.round(top + 20) + 'px';
                left = Math.round((Math.min(winSize().width - 200, left))) + 'px';
                let position = 'absolute';
                let right = 'inherit';

                if(IS_MOBILE) {
                    top = '52px';
                    right = '28px';
                    left = 'inherit';
                    position = 'fixed';
                }

                let propsUt = {
                    position,
                    left,
                    right,
                    top, 
                    visible: true
                }
                
                setpropsUtiles(propsUt);

            }else{
                setpropsUtiles({...propsUtiles, visible:false});
            }
        }
    }

    const clickPage = e => {
        if(loading) return;
        
        let {id, tagName, className, naturalWidth, naturalHeight, src, alt, href, parentElement} = e.target;

        let width = naturalWidth;
        let height = naturalHeight;

        let dataWh = e.target.getAttribute('data-wh');
        if(dataWh){
            let Wh = dataWh.split("x");
            width = parseInt(Wh[0]);
            height = parseInt(Wh[1]);
        }

        if(parentElement.tagName === "A" && !id && parentElement.href.indexOf('media/') > -1){
            id = parentElement.href.split('media/')[1];
        }
        if(tagName === 'A' && !id && href.indexOf('media/') > -1){
            id = href.split('media/')[1];
        }

        if(tagName === 'P' || className === 'section' || id === 'sc') {
            return;
        }

        
        if(tagName === 'SPAN' && className === 'note' && id){
            console.log(tagName, id, className)

            // showUtiles(false);

            viewNote(e.target);

            return;
        }

        if(tagName === 'SPAN' && className === 's' && id){
            return;
        }

/*
        e.preventDefault()

        console.log(dataTid.medias[id], id, e.target, tagName, className, parentElement.tagName, parentElement.className)

        const {left, top} = e.target.getBoundingClientRect();
        const x = e.pageX - left;
        const y = e.pageY - top;

        if( x < 40 && y < 44 && parentElement.className === 'txae' && dataTid.medias[id].i){
            console.log(id, dataTid.medias[id].i, 'is_txtALt')
            setModalData({...dataTid.medias[id], id, istxt:true, URL_TID});
            setOpen(true);
            return;
        }       
*/
        if(id){
            if(!dataTid.medias[id]){
                return;
            }
            e.preventDefault();
            setModalData({...dataTid.medias[id], id, istxt:className === "txa", URL_TID});
            setOpen(true);
        }else{
            if(tagName === 'IMG'){
                if(className==="ic" && parentElement.tagName === "A" && parentElement.className === "im-icon"){
                    e.preventDefault();
                    setModalData({src:parentElement.href, width, height, alt, m:1});
                    setOpen(true);
                }
                if(className==="im" || className==="im l" || className==="im r" || className==="im sy"){
                    e.preventDefault();
                    setModalData({src, width, height, alt, m:1});
                    setOpen(true);                
                }
            }else if(tagName === 'A'){
                if(className === "im-icon"){
                    e.preventDefault();
                    setModalData({src:href, width, height, alt, m:1});
                    setOpen(true);
                }
            }
        }
    }

    useEffect(() => {
        if(dataTid && dataTid.TID === parseInt(TID)){
            setpropsUtiles({});

            if(!idn || parseInt(idn) === 0) {
                if(sesionTid.idn !== 0 ){
                    history.push(`/tid/${TID}/page/${sesionTid.idn}`);
                }else{
                    history.push(`/tid/${TID}/page/${dataTid.indice[0].id}`);
                }                
                return;
            }
            const fetchPage = async () => {

                setLoading(true);                

                const data = { TID, idn: parseInt(idn)};

                try {
                    const result = await axios.post(`${API_BASE}get_page.php`, data);

                    if(result.data.r === 0){
                        mostrarAlerta(result.data.text, "Error en página");
                        setLoading(false);
                        return;
                    }
                    
                    let htmlNode = result.data.nodo
                    htmlNode = htmlNode.replace(/src="me\//g, `src="${URL_TID}me/`);
                    htmlNode = htmlNode.replace(/href="me\//g, `href="${URL_TID}me/`);
                    htmlNode = htmlNode.replace(/href="me\//g, `href="${URL_TID}me/`);
                    htmlNode += '<div class="finh" style="visibility: hidden;"></div>';

                    setImagesLoaded(false); 
                    guardaSesion(idn);

                    window.scrollTo(0,0);

                    paginaRef.current.innerHTML = htmlNode;

                    //findAndReplace

                    // sethtmlPage(htmlNode)
                    
                    onImagesLoaded();
                    
                }catch (error) {
                    mostrarAlerta(error, "Error al obtener página");
                    setLoading(false);
                }
            }
            fetchPage();
        }
        // eslint-disable-next-line
    }, [idn, TID, URL_TID, dataTid]);


    useEffect(() => {
        setTID(TID);
        // eslint-disable-next-line
    }, []);


    const [openPanel, setopenPanel] = useState(false);

    const toggleDrawer = open => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }    
        setopenPanel(open);
    }


    const [openPreguntas, setopenPreguntas] = useState(false);

    const toggleDrawerPreguntas = open => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }    
        setopenPreguntas(open);
    }


    const [tidConfig] = useLocalStorage('tidConfig', {aumentarTxt:false});

    return (
        <div className={tidConfig.aumentarTxt ? 'aumentarTxt' : null}>         

            {open && <Media open={open} handleClose={handleClose} modalData={modalData} /> }

            { (dataTid!==null && idn) &&    
                <NoteNode title={getNodoData(parseInt(idn)).t} />
            }
            { (dataTid!==null && idn) &&    
                <Utiles propsUtiles={propsUtiles} setpropsUtiles={setpropsUtiles} noteSelectedText={noteSelectedText} resaltarSelectedText={resaltarSelectedText} unResaltarSelectedText={unResaltarSelectedText} />
            }

        <PreguntasState>

            { (dataTid!==null && idn) && 
                <>
                <SwipeableDrawer
                    anchor="right"
                    disableSwipeToOpen = {true}
                    open={openPanel}
                    onClose={toggleDrawer(false)}
                    onOpen={toggleDrawer(true)}
                >
                    <Panel setopenPanel={setopenPanel} title={getNodoData(parseInt(idn)).t} panelnots={panelnots} panelresums={panelresums} eliminarNotas={eliminarNotas} eliminarResumenes={eliminarResumenes} />
                </SwipeableDrawer>

                <SwipeableDrawer
                anchor="right"
                disableSwipeToOpen = {true}
                open={openPreguntas}
                onClose={toggleDrawerPreguntas(false)}
                onOpen={toggleDrawerPreguntas(true)}
                className="panelPreguntas"
                >
                    <PanelPreguntas setopenPreguntas={setopenPreguntas} idn={idn} title={getNodoData(parseInt(idn)).t} />
                </SwipeableDrawer>
                </>
            }


            <Header 
                setOpenMenu={setOpenMenu} 
                paginaRef={paginaRef}
                openMenu={openMenu}
                imagesLoaded = {imagesLoaded}
                isTid = {true}
                clearNode={clearNode}
                updateUtiles={updateUtiles}
                loading={loading}
                utilesVisible={propsUtiles.visible}
                showUtils={panelresums.length > 0 || panelnots.length > 0}
                setOpenPanelUtils={setopenPanel}
                openUtils={openPanel}
                showPreguntas={true}
                setOpenPanelPreguntas={setopenPreguntas}
                openPreguntas={openPreguntas}
                titulo={!loading ? dataTid.materia : loadingTxt} />

        </PreguntasState>

            {((dataTid && dataTid.TID === parseInt(TID)) || !(loadingTxt || loading)) ?
            <>
            < br/>< br/>
            {/* <div dangerouslySetInnerHTML={{ __html: htmlPage }}></div> */}
                <div ref={paginaRef} id="sc" className="section" style={{minHeight:`${minWinHeight}px`}} onMouseUp={onEndSelect} onTouchEnd={onEndSelect} onTouchCancel={onEndSelect} onClick={clickPage}>
                    
                </div>
                {imagesLoaded &&
                    <Scroll paginaRef={paginaRef} idn={parseInt(idn)} />
                }
            </>
            :
            <>
                <div className="section">
                    <p className="th"><Skeleton /></p>
                    <Skeleton count={15} />
                </div>
            </>
            }

            <Footer loading={loading} idn={parseInt(idn)} history={history} />

        </div>        
    );
}

export default Page;