import React, { createContext, useState, useEffect, useContext } from 'react';
import axios from 'axios';

import alertaContext from './alertas/alertaContext';
import useLocalStorage from 'hooks/useLocalStorage';

import {API_BASE, VERAPP} from 'components/constants';

export const TidDataContext = createContext();

const TidDataProvider = ({children, dni}) => {
   
    const [dataTid, setDataTid] = useState(null);
    const [sesionTid, setSesionTid] = useState(null);
    const [TID, setTID] = useState(null);
    const [realPercentView, setRealPercentView] = useState(0);

    const { mostrarAlerta } = useContext(alertaContext);
    const [uuidDevice] = useLocalStorage('uuidDevice');

    let guardandoSesion = false;
    let dataSesionSendTmp;

    const setAvance = (avance=null) => {

        const dataAvance = avance ? avance : sesionTid.avanceNodos;

        let totalPoints = 0;
        let ptosvistos = 0;
        
        const indice = dataTid.indice;
        Object.keys(indice).forEach(key => 
            {
                const puntaje = indice[key].p;
                totalPoints += puntaje;                
                if(dataAvance["id"+indice[key].id]){
                    ptosvistos += puntaje;
                }else{
                    //console.log("setAvance", indice[key])
                }
            }       
        );
        // compara si hay cambio de porcentaje y mando sesion al server
        const newPercent = Math.ceil((ptosvistos * 100) / totalPoints);
        if(realPercentView !== newPercent && realPercentView > 0){
            console.log('cambio porcentaje', realPercentView, newPercent)
            if(avance){
                console.log("debe guardar avance");                
                guardaSesion(sesionTid.idn, null, avance);
            }
        }
        setRealPercentView(newPercent);
    }

    const getNodoData = idn => dataTid ? dataTid.indice.filter( obj => obj.id === parseInt(idn) )[0] : {id:null, t:null};

    const guardaSesion = async (idnSes, sesion = null, avanceNodos = null) => {

        idnSes = parseInt(idnSes);

        if(guardandoSesion) return;

        if(!uuidDevice){
            console.log("Error not uuidDevice defined");
            return;
        }

        const data = { dni, idn:idnSes, TID, VERAPP, uuidDevice};

        if(!dataSesionSendTmp){
            // recien inicia
            dataSesionSendTmp = {...sesionTid};
        }

        let hayCambio = false;
        
        if(avanceNodos){
            // cambia avance
            data.avanceNodos = avanceNodos;
            dataSesionSendTmp.avanceNodos = avanceNodos;
            hayCambio = true;
            setSesionTid({...sesionTid, avanceNodos});
        }

        if(sesion){
            // cambia sesion
            data.sesion = sesion;
            dataSesionSendTmp.sesion = sesion;
            hayCambio = true;
            setSesionTid({...sesionTid, sesion});
        }

        if(dataSesionSendTmp.idn !== idnSes){
            // cambia idn
            data.idn = idnSes;
            dataSesionSendTmp.idn = idnSes;
            hayCambio = true;
            setSesionTid({...sesionTid, idn:idnSes});
        }

        if(hayCambio){
            guardandoSesion = true;
            try {
                const result = await axios.post(`${API_BASE}set_sesion.php`, data);
                guardandoSesion = false;
                if (parseInt(result.data.r) === 0){
                    mostrarAlerta(result.data.text, "TID");
                }
            }catch (error) {
                guardandoSesion = false;
                mostrarAlerta(error, 'Error');
            }
        }else{
            console.log("no guarda");
        }
    };

    const importarSesion = async () => {

        const data = { TID, dni, id: dataTid.importa_ses_obj.id, VERAPP };

        try {
            const result = await axios.post(`${API_BASE}get_sesion.php`, data);
            
            if (parseInt(result.data.r) === 0){
                mostrarAlerta(result.data.text, "Sesión");
            }else{

                let dTid = {...dataTid};
                dTid.importa_ses_obj = JSON.parse(result.data.importa_ses_obj);

                setDataTid(dTid);
                setSesionTid({
                    ...sesionTid, 
                    avanceNodos:JSON.parse(result.data.avanceNodos),
                    sesion:JSON.parse(result.data.sesion),
                    idn: result.data.idn
                })
            }
        }catch (error) {
            mostrarAlerta(error, 'Error');
        }
    }

    useEffect(() => { 
        if(dataTid){
            setAvance();
        }
        // eslint-disable-next-line
    }, [dataTid]);

    useEffect(() => { 
        // console.log(TID, dni);
        if(TID && dni){
            if(!dataTid || dataTid.TID !== TID){
                setRealPercentView(0);
                const fetchTid = async () => {
                    //setCargando(true);

                    if(!uuidDevice){
                        console.log("Error uuidDevice");
                        return;
                    }

                    const data = {TID, dni, uuidDevice};

                    try {
                        const result = await axios.post(`${API_BASE}get_tid.php`, data);

                        console.log(result);

                        if(result.data.r === 0){
                            mostrarAlerta(result.data.text, "Error");
                            return;
                        }

                        console.log(" dni:"+dni+"\n TID:"+TID+"\n id_carrera:"+result.data.dataTid.id_carrera, "\n id_materia:"+result.data.dataTid.id_materia);
                        
                        //console.log('eliminar linea !!!!!!!!!!!!!!!!');
                        //dTid.consulta_doc = true;

                        /****
                         *                          * 
                         * Ordenar la info entre tidContext y page... deberia manejar todo el tidContext, incluso el idTID
                         * 
                         */

                        setSesionTid(result.data.sesionTid);                        
                        setDataTid(result.data.dataTid);
                        //setIdn(sTid.idn);
                        
                    }catch (error) {
                        mostrarAlerta(error, "Error");
                    }
                    //setCargando(false);
                }
                fetchTid();
            }
        }
        // eslint-disable-next-line
    }, [TID, dni]);

    return (
        <React.Fragment>
            <TidDataContext.Provider value={{dataTid, sesionTid, setTID, getNodoData, realPercentView, setAvance, importarSesion, guardaSesion}}>
                {children}
            </TidDataContext.Provider>
        </React.Fragment>
    )
}

export default TidDataProvider;