import { PUSH_LOADIG, READY_PUSH, PUSH_SUPPORTED, PUSH_ESTASUSCRIPTO, PUSH_PARAMS, PUSH_ERRORSUSCRIBE, PUSH_USCONSENT} from 'types';

export default (state, action) => {
    switch(action.type) {
        case PUSH_LOADIG:
            return {
                ...state,
                loadingPush: action.payload
            }
        case PUSH_SUPPORTED:
            return {
                ...state,
                soportaNotificaciones: action.payload
            }
        case PUSH_ESTASUSCRIPTO:
            return {
                ...state,
                estaSuscripto: action.payload
            }
        case PUSH_PARAMS:
            return {
                ...state,
                pushToken: action.payload.pushToken,
                hwid: action.payload.hwid,
                userId: action.payload.userId
            }
        case PUSH_ERRORSUSCRIBE:
            return {
                ...state,
                errorsuscribe: action.payload
            }
        case PUSH_USCONSENT:
            return {
                ...state,
                userConsent: action.payload
            }
        case READY_PUSH:
            return {
                ...state,
                isReady: action.payload
            }
        default:
            return state;
    }
}