import React, {useState, useEffect, useContext} from 'react';
import { useLocation, Link } from "react-router-dom";
import { UsuarioDataContext } from 'context/usuarioContext';

import PajaroImg from 'img/pajaro.png';

export default function P404() {

    const {dataUsuario} = useContext(UsuarioDataContext);
    
    let location = useLocation();

    const [enlace, setEnlace] = useState('https://alumnos.ies21.edu.ar/');

    useEffect(() => {
        if(dataUsuario){
            setEnlace(`https://alumnos.ies21.edu.ar/autlgnapp.asp?t=${dataUsuario.tokenSistemas}&pagina=tids`);
        }
        // eslint-disable-next-line
    }, [dataUsuario]);

    return (
        <div className="seccion" id="seccion_cont">
            <div className="contp">
                <table width="100%">
                    <tbody>
                    <tr>
                        <td>
                            <br /><p className="thome">Oh!<br />
                            Página <code>{location.pathname}</code> no encontrada</p><br />
                            <p>Esta página ya no está disponible o cambió de dirección.<br />Revise el acceso si es correcto.</p>                                            
                        </td>
                        <td width="138">
                            <img src={PajaroImg} alt="" />
                        </td>
                    </tr>
                    </tbody>
                </table>
                

            </div>
            <div className="contp">
                <p><Link to={'/'} >Home</Link></p>
                <p><a href={enlace} target="_blank" rel="noopener noreferrer">Autogestión</a></p>
            </div>
        </div>
    )
}