import React, { useReducer } from 'react';
import pushReducer from './pushReducer';
import pushContext from './pushContext';
import useLocalStorage from 'hooks/useLocalStorage';

import { 
    PUSH_LOADIG, 
    READY_PUSH, 
    PUSH_SUPPORTED, 
    PUSH_ESTASUSCRIPTO, 
    PUSH_PARAMS, 
    PUSH_ERRORSUSCRIBE, 
    PUSH_USCONSENT 
} from 'types';

import clienteAxios from 'config/axios';
import {VERAPP, device_data} from 'components/constants';

async function askUserPermission() {
    return await Notification.requestPermission();
}

const PushState = props => {

    const NotificationSupported = "Notification" in window;
    const [uuidDevice] = useLocalStorage('uuidDevice');
    
    const initialState = {        
        soportaNotificaciones: false,
        isReady: false,
        loadingPush: false,
        estaSuscripto: false,
        pushToken: null,
        hwid: null,
        userId: null,
        errorsuscribe: null,
        userConsent: NotificationSupported ? Notification.permission : false,
    }

    const [ state, dispatch ] = useReducer(pushReducer, initialState);

    const initPush = (dni) => {
        
        // console.log("initPush");

        const Pushwoosh = window.Pushwoosh;

        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });

        Pushwoosh.push(["init", {
            logLevel: 'error', // possible values: error, info, debug
            applicationCode: 'C7E03-9A6B0',
            safariWebsitePushID: 'ies21.edu.ar',
            defaultNotificationTitle: 'IES 21',
            defaultNotificationImage: '/logo192.png',
            autoSubscribe: false,
            userId: dni
        }]);

        Pushwoosh.push(function(api) {
            // Register user ID
            api.registerUser(dni);
        });

        Pushwoosh.push(['onReady', function() {
            // console.log('Pushwoosh ready');
            onReady();
        }]);

        Pushwoosh.push(['onUnsubscribe', function() {
            dispatch({
                type: PUSH_ESTASUSCRIPTO,
                payload: false
            });
            dispatch({
                type: PUSH_LOADIG,
                payload: false
            });
            getPWParams();
        }]);

        Pushwoosh.push(['onSubscribe', function() {
            dispatch({
                type: PUSH_ESTASUSCRIPTO,
                payload: true
            });
            dispatch({
                type: PUSH_LOADIG,
                payload: false
            });
            getPWParams();
        }]); 
    }

    const onReady = () => {

        // console.log('onReady')

        const Pushwoosh = window.Pushwoosh;

        dispatch({
            type: PUSH_SUPPORTED,
            payload: Pushwoosh.isAvailableNotifications()
        });

        getExixtingSubscription();
    }

    const getExixtingSubscription = async () => {

        const Pushwoosh = window.Pushwoosh;

        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });

        try{
            const estaSuscripto = await Pushwoosh.isSubscribed();

            dispatch({
                type: PUSH_ESTASUSCRIPTO,
                payload: estaSuscripto
            });

            dispatch({
                type: READY_PUSH,
                payload: true
            });
            
            getPWParams();

        }catch (e){
            console.log(e);
            setTimeout(() => {
                onReady();
            }, 4000);
        }
        
        dispatch({
            type: PUSH_LOADIG,
            payload: false
        });
    };

    const getPWParams = async () => {
        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });

        const Pushwoosh = window.Pushwoosh;        

        const params = await Pushwoosh.getParams();
        
        // console.log(params);
        updateWebPushToken(params);

        dispatch({
            type: PUSH_PARAMS,
            payload: params
        });

        dispatch({
            type: PUSH_LOADIG,
            payload: false
        });       
    }

    const updateWebPushToken = async params => {

        if(!uuidDevice){
            console.log("Error uuidDevice in state push");
            return;
        }

        const data = {
            hwid:params.hwid,
            pushToken: params.pushToken ? params.pushToken : '',
            estaSuscripto: params.pushToken ? true : false,
            dni: params.userId,
            device_data: device_data,
            VERAPP: VERAPP,
            uuidDevice: uuidDevice
        }
        // console.log('updateWebPushToken')
        // console.log(data)

        try {
            const resultado = await clienteAxios.post('update-web-pushtoken.php', data);
            console.log(resultado);

            if(resultado.data.r === 0 ){
                console.log(resultado.data.text);
                return;
            }
            if(resultado.data === undefined){
                console.log(resultado);
                return;
            }

            console.log('Se regsitro hwid '+ params.hwid)
        } catch (error) {
            console.log(error);
        }
    }

    const onClickUnsubscribe = () => {
        if(!state.estaSuscripto){
            return;
        }
        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });
        const Pushwoosh = window.Pushwoosh;
        Pushwoosh.unsubscribe();
    };

    const onClickSubscribe = () => {
        if(state.estaSuscripto){
            return;
        }
        console.log('onClickSubscribe');

        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });

        const Pushwoosh = window.Pushwoosh;        
        Pushwoosh.subscribe();
    };

    const onClickAskUserPermission = (onAccept = null) => {
        console.log('onClickAskUserPermission');
        dispatch({
            type: PUSH_LOADIG,
            payload: true
        });
        dispatch({
            type: PUSH_ERRORSUSCRIBE,
            payload: false
        });
        askUserPermission().then(consent => {
            dispatch({
                type: PUSH_USCONSENT,
                payload: consent
            })
            if (consent !== "granted") {
                dispatch({
                    type: PUSH_ERRORSUSCRIBE,
                    payload: {
                        name: "Consentimiento denegado",
                        message: "Has denegado el consentimiento para recibir notificaciones",
                        code: 0
                        }
                });
                dispatch({
                    type: PUSH_LOADIG,
                    payload: false
                });
            }else{
                dispatch({
                    type: PUSH_LOADIG,
                    payload: false
                });
                if(onAccept !== null)  onAccept();                
            }            
        });
    };


    return (
        <pushContext.Provider
            value={{
                isReady: state.isReady,
                soportaNotificaciones: state.soportaNotificaciones,
                loadingPush: state.loadingPush,
                estaSuscripto: state.estaSuscripto,
                pushToken: state.pushToken,
                hwid: state.hwid,
                userId: state.userId,
                userConsent: state.userConsent,
                errorsuscribe: state.errorsuscribe,
                initPush,
                onClickUnsubscribe,
                onClickSubscribe,
                onClickAskUserPermission,
                askUserPermission
            }}
        > 
            {props.children}
        </pushContext.Provider>
    )
}

export default PushState;