import React, {useState, useEffect, useContext, useRef} from 'react';
import { useParams } from "react-router-dom";

import {TidDataContext} from 'context/tidContext';
import {get_urlTid} from 'components/constants';
import Header from 'components/header/header';

export default function Editorial({openMenu, setOpenMenu}) {

    const { TID } = useParams();
    
    const {dataTid, setTID} = useContext(TidDataContext);

    const [loading, setLoading] = useState(true);

    const URL_TID = get_urlTid(TID);

    const paginaRef = useRef();

    useEffect(() => {
        if(dataTid){
            
            let htmlNode = '';

            const intMed = Object.keys(dataTid.medias);

            intMed.forEach( key => {
                    const media = dataTid.medias[key];
                    if(media.inst && media.inst === 1){
                        if(media.m === 20){
                            htmlNode += `${media.incod}<hr>`;
                        }else if(media.m === 1){
                            htmlNode += `<img src="${URL_TID}${media.src}" class="im"><hr />`;
                        }else if(media.m === 0){
                            htmlNode += `<a href="${media.href}">${media.txt}</a><hr />`;
                        }else if(media.m === 2){
                            htmlNode += `<p>${media.t}</p><hr />`;
                        }
                    }
                }
            );

            if(htmlNode === ''){
                htmlNode = '<p>Sin información editorial.</p>';
            }

            paginaRef.current.innerHTML = htmlNode;

            window.scrollTo(0,0);

            setLoading(false);
        }
        // eslint-disable-next-line
    }, [URL_TID, dataTid]); 


    useEffect(() => { 
        setTID(TID);
        // eslint-disable-next-line
    }, []);  

    return (
        <React.Fragment>

            <Header setOpenMenu={setOpenMenu} openMenu={openMenu} isTid={true} loading={loading} titulo={!loading ? 'Editorial: '+ dataTid.materia : 'Cargando datos...'} />

            <div className="section institucionales">
                {dataTid ? <h1>{dataTid.materia}</h1> : null }
                <div ref={paginaRef}></div>
            </div>
        </React.Fragment>        
    );
}