import React, { useState } from 'react';

import Modal from '@material-ui/core/Modal';
import { makeStyles } from '@material-ui/core/styles';
import parse from 'html-react-parser';
import PinchZoomPan from 'react-responsive-pinch-zoom-pan';

import { winSize, winSizeModal } from 'components/constants';


function getModalStyle() {
    const top = 50;
    const left = 50;
  
    return {
        top: `${top}%`,
        left: `${left}%`,
        fontSize: '16px',
        lineHeight: '24px',
        transform: `translate(-${top}%, -${left}%)`,
    };
}


const useStyles = makeStyles(theme => ({
    texto: {
        position: 'absolute',
        maxWidth: winSize().width - 20,
        width: winSizeModal(),
        maxHeight: winSize().height - 20,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
        overflowY: 'auto',
    },
    info: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
        overflowY: 'auto',
    },
    codigo: {
        position: 'absolute',
        maxWidth: winSize().width - 20,
        width: winSize().width,
        maxHeight: winSize().height - 10 - 52,
        backgroundColor: '#00aae4',
        boxShadow: theme.shadows[5],
        padding: 0,
        marginTop: 22,
        overflowY: 'auto',
    },
    imagen: {
        position: 'absolute',
        width: winSize().width,
        maxHeight: winSize().height,
        boxShadow: theme.shadows[5],
        overflowY: 'auto',  
        padding: '10px',
        backgroundColor: '#fff',
    },
    img: {
        backgroundColor: '#fff',
    },
    h2: {
        fontSize: 16,
        paddingRight: 20,
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        paddingLeft: 20,
        color:'#fff'
    },
}));

export default function Media({open, handleClose, modalData}) {
  
    

    const classes = useStyles();
    const [modalStyle] = useState(getModalStyle);
    const [showInfo, setShowInfo] = useState(false);

    if(Object.keys(modalData).length === 0) return null;

    let mediaRender = <div></div>;
    
    
    if(modalData.istxt){
        modalData.m = 2;
    }

    let aspect = modalData.w / modalData.h;
    let responsiveW = window.innerWidth;
    let responsiveH = Math.round(responsiveW / aspect);
    let fullscreen = false;

    responsiveW = modalData.w
    responsiveH = modalData.h

    if(modalData.w < 1){
        responsiveW = '100%';
        responsiveH = '100%';
        fullscreen = true;
    }

    let classRender = null;


    const infoalt = !fullscreen && modalData.id && modalData.i && modalData.i!=='' && modalData.m !== 1 && modalData.m !== 2 && modalData.m !== 17;

    const onloadImg = e => {
        e.target.className = document.getElementById('mediaOnload').className.replace(' bgmm', '');
    }

    switch (modalData.m) {
        case 1:
            // imagen
            mediaRender =   <div id="mediaOnload" style={modalStyle} className={`${classes.imagen} bgmm`}>
                                <PinchZoomPan zoomButtons={false} position={'center'}>
                                    <img alt={modalData.alt} src={modalData.src} className={classes.img} onLoad={onloadImg} />
                                </PinchZoomPan>
                            </div>;
            break;
        case 2:
            // texto
            mediaRender =   <div style={modalStyle} className={classes.texto}>
                                {(modalData.t !== modalData.c && modalData.t !== '') && <h2>{modalData.t}</h2>}
                                <p>
                                    {modalData.c ? modalData.c : null}
                                    {modalData.i ? parse(modalData.i) : null}
                                </p>
                            </div>
            break;
        case 17:
            // codigo
            mediaRender =   <div style={modalStyle} className={classes.codigo}>
                                <h2 className={classes.h2}>{modalData.t}</h2>
                                {modalData.c ? parse(modalData.c) : null}
                                {modalData.i ? parse(modalData.i) : null}
                            </div>
            break;
                
        case 3:
            // video youtube
            /*
            mediaRender =   <div style={{maxWidth:`${responsiveW}px`, maxHeight:`${responsiveH}px`}}>
                                <div className="ifrav">
                                    <iframe  width={responsiveW} height={responsiveH} src={`https://www.youtube.com/embed/${modalData.id}?showinfo=0&autoplay=1`} frameBorder="0" allowFullScreen title={modalData.t}>{modalData.t}</iframe>
                                </div>
                            </div>
                            */
            mediaRender =   <div className="ifravok bgmm">
                                <iframe width={responsiveW} height={responsiveH} src={`https://www.youtube-nocookie.com/embed/${modalData.id}?rel=0&showinfo=0&autoplay=1`} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen title={modalData.t}></iframe>
                            </div>
            break;
        case 34:
            // video mp4
            mediaRender =   <div className="ifrav" style={{paddingBottom:0}}>
                                <video width={responsiveW} height={responsiveH} controls autoplay>
                                    <source src={`${modalData.URL_TID}me/${modalData.id}.mp4`} type="video/mp4" />
                                    <p>Puede que esta multimedia no se reproduzca en su navegador</p>
                                </video>
                            </div>
            break;
        case 20:
        case 8:
            // widget
            // interactiva
            let scrolling = "yes";
            if(modalData.ps===1){
                scrolling = "no";
            }
            
            classRender = fullscreen ? 'fscreen media' : 'media';

            mediaRender =   <div className="ifrai">
                                { modalData.m !== 20 ?
                                    <iframe width={responsiveW} height={responsiveH} className={modalData.ps===1 ? 'toct' : null} src={`${modalData.URL_TID}me/${modalData.id}`} scrolling={scrolling} frameBorder="0" allowFullScreen title={modalData.t}>{modalData.t}</iframe>
                                : 
                                    <div>{parse(modalData.incod)}</div>
                                }
                            </div>
            break;
        case 5:
            // audio
            mediaRender =   <div className="ifrav">
                                <audio controls autoPlay>
                                    <source src={`${modalData.URL_TID}me/${modalData.id}.mp3`} type="audio/mpeg" />
                                    <p>Puede que esta multimedia no se reproduzca en su navegador</p>
                                </audio>
                            </div>
            break;      

        default:
            break;
    }

    

    return (
        <Modal
            open={open}
            onClose={handleClose}
        >
            { [1, 2, 3, 17].indexOf(modalData.m) > -1 ? 
                <>
                    <header className="media">
                        <button className="back" onClick={handleClose}></button> 
                    </header>
                    {mediaRender}
                    {modalData.f ? <div className="fuente-abs">Fuente: {modalData.f}</div> : null}
                </>
            :
                <>
                    <header className="conten">
                        <button className="back" onClick={handleClose}></button> 
                        <h1 style={!infoalt ? {paddingLeft:20} : {paddingLeft:68}}>{modalData.t}</h1>
                        { infoalt && 
                            <div onClick={() => setShowInfo(!showInfo)} className={showInfo ? 'info active' : 'info'}></div>
                        }
                    </header>
                    <section className={classRender ? classRender : 'media'}>
                        { (infoalt && showInfo) &&
                                <div className='info_media'>
                                    <p>{parse(modalData.i)}</p>
                                </div>
                        }
                        {mediaRender}
                        {(modalData.f && modalData.f!=='') ? <p className="fuente">Fuente: {modalData.f}</p> : null}
                    </section>
                </>
            }
        </Modal>
    );
}