import React, {useState, useEffect, useContext, useRef} from 'react';
import { useHistory, useParams } from "react-router-dom";

import axios from 'axios';

import useLocalStorage from 'hooks/useLocalStorage';
import {TidDataContext} from 'context/tidContext';
import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';
import {API_BASE, VERAPP} from 'components/constants';

import Header from 'components/header/header';

export default function Consulta({openMenu, setOpenMenu}) {

    let history = useHistory();    

    const { TID } = useParams();

    const {dataTid, sesionTid, setTID} = useContext(TidDataContext);    
    const {dataUsuario} = useContext(UsuarioDataContext);
    const { mostrarAlerta } = useContext(alertaContext);
    const [uuidDevice] = useLocalStorage('uuidDevice');

    const mensajeRef = useRef();

    const [loading, setLoading] = useState(true);
    const [dataTutor, setdataTutor] = useState({});

    const iraNodo = id => {
        history.push(`/tid/${TID}/page/${id}`);
        setOpenMenu(false);
    }

    const sendAction = async () => {
        const mensaje = mensajeRef.current.value.trim();

        if( mensaje.length < 10 ) {
            mostrarAlerta("Mensaje demasiado corto.", "Enviar consulta al tutor");
            return;
        }

        if(!uuidDevice){
            console.log("Error uuidDevice");
            return;
        }

        const data = {
            dni: dataUsuario.dni, 
            email: dataUsuario.email,
            nombre: `${dataUsuario.apellidos}, ${dataUsuario.nombres}`,
            TID: parseInt(TID),
            id_carrera: dataTid.id_carrera,
            id_materia: dataTid.id_materia,
            mensaje,
            idn: sesionTid.idn,
            VERAPP,
            uuidDevice,
        }

        setLoading(true);

        try {
            const result = await axios.post(`${API_BASE}consulta_tutor.php`, data);
            mostrarAlerta(result.data.text, "Consulta");

        }catch (error) {
            mostrarAlerta(error, "Consulta");
        }
        
        setLoading(false);
    }

    useEffect(() => {
        if(dataTid && dataUsuario){

            if(!dataTid.consulta_doc){
                iraNodo(sesionTid.idn);
            }

            const fetchTutor = async () => {

                setLoading(true);

                const data = { dni:dataUsuario.dni, TID:parseInt(TID), VERAPP };

                try {
                    const result = await axios.post(`${API_BASE}get_datos.php`, data);
                    console.log(result);

                    if(result.data.r === 0 ){
                        mostrarAlerta(result.data.text, "Consulta");
                        setLoading(false);
                        return;
                    }
                    
                    const {nombres, apellidos, email} = result.data;

                    setdataTutor({nombres, apellidos, email});

                    if(email === ''){
                        mostrarAlerta(`No hay tutor asignado a <strong>${dataTid.materia}</strong> para consultas.`, "Consulta");
                    }

                }catch (error) {
                    mostrarAlerta(error, 'Consulta');
                }
                setLoading(false);
            }
            fetchTutor();
        }
        // eslint-disable-next-line
    }, [dataTid, dataUsuario]);

    useEffect(() => { 
        setTID(TID);
        // eslint-disable-next-line
    }, []); 
    
    return (
        <>

            <Header setOpenMenu={setOpenMenu} openMenu={openMenu} loading={loading} sendAction={sendAction} titulo={!loading ? 'Mensaje al Docente' : 'Cargando datos...'} />

            { !loading && 
            <div className="section media flex nosombra" style={{paddingLeft:0, paddingRight:0, position:'absolute'}}>   
                { dataTutor && dataTutor.email !== '' ? 
                    <>
                        <div className="contp f16">
                            <strong>De:</strong> {dataUsuario.email}
                        </div>
                        <div className="contp f16">
                            <strong>Para:</strong> {dataTutor.nombres} {dataTutor.apellidos}
                        </div>
                        <div className="contp bgmgris" onClick={() => mensajeRef.current.focus()}>
                            Explicá brevemente si tenés alguna duda con los conceptos en el TID de {dataTid.materia}.
                        </div>
                        <textarea className="txfull" autoFocus ref={mensajeRef}></textarea>
                    </>
                :
                    <div className="contp f16">No hay datos de tutor asignado a {dataTid.materia}.</div>
                }
            </div>
            }
        </>        
    );
}