import React, {useState, useContext, useEffect} from 'react';

import HacerPregunta from 'components/preguntas/hacerPregunta';

import { UsuarioDataContext } from 'context/usuarioContext';
import { TidDataContext } from 'context/tidContext';

import preguntasContext from 'context/preguntas/preguntasContext';
import alertaContext from 'context/alertas/alertaContext';

import {getWidthPanmat} from 'components/constants';

import './panel.css';
import VerPregunta from 'components/preguntas/verPregunta';
import Pregunta from 'components/preguntas/pregunta';
import SubLoading from 'components/loading';


export default function PanelPreguntas({setopenPreguntas, title, idn}) {
    
    const {dataUsuario} = useContext(UsuarioDataContext);
    const {dataTid} = useContext(TidDataContext);

    const [preguntar, setPreguntar] = useState(false);
    const [preguntaId, setPreguntaId] = useState(false);

    const { obtenerPreguntas, preguntas, total, mensaje, loading, searchQuestions, resetMensaje } = useContext(preguntasContext);
    const { mostrarAlerta } = useContext(alertaContext);

    const data = {
        dni: dataUsuario.dni,
        idn: parseInt(idn),
        TID: dataTid.TID,
        id_materia: dataTid.id_materia,
        id_carrera: dataTid.id_carrera,
        esDocente: dataUsuario.esDocente
    };

    const handleChangeSearch = e => {
        searchQuestions(e.target.value)
    }

    let heightPanelCont = (window.innerHeight - 52) + 'px';

    // Obtener preguntas cuando carga el componente
    useEffect(() => {
        // si hay un error
        if(mensaje) {
            mostrarAlerta(mensaje);
            resetMensaje();
        }
        // eslint-disable-next-line
    }, [mensaje]);

    useEffect(() => {
        obtenerPreguntas(data);
        // eslint-disable-next-line
    }, []);

    return(
        <>
        <div id="panelder">
            <div className="headpan">
                <div className="wraperpan">
                    <button className="back" onClick={()=>setopenPreguntas(false)}></button> 
                    <div className="panmat" style={{width:getWidthPanmat()}}>Preguntas en "{title}"</div>
                </div>
            </div>            
            <div className="pandercont panelOverflow wfijo mt52" style={{padding:'15px', height:heightPanelCont}}>
                {preguntar &&
                    <> 
                      <HacerPregunta isOpen={setPreguntar} dataUsuario={dataUsuario} idn={idn} dataTid={dataTid} />
                    </>
                }
                {preguntaId !== false && 
                    <>
                        <VerPregunta isOpen={setPreguntaId} preguntaId={preguntaId} dataUsuario={dataUsuario} />
                    </>
                }
                {(!preguntar && preguntaId === false) &&
                <>
                    <div className="fx-c fx-jsb" style={{marginBottom: '15px'}}>
                        <div className="question-overview--filter-results-summary--2k-XZ">
                            {!(loading || total < 1) && <>{total} preguntas en esta página</>}
                        </div>
                        <button type="button" className="btn btn-sm btn-quintinary" onClick={()=>setPreguntar(true)}>Preguntar</button>
                    </div>

                    {loading ? <p><SubLoading /> Cargando preguntas...</p> : 
                        <>
                        {total === 0 ? <p>No hay preguntas realizadas.</p>  :                            
                            <>
                            {total > 3 ?
                            <div className="question-search">
                                <input type="text" className="search" id="busqueda" onChange={handleChangeSearch} placeholder="Buscá una pregunta" />
                            </div>
                            : null }

                            {total !== preguntas.length ? <p style={{marginBottom:'15px'}}>{preguntas.length} resultados de {total}</p> : null}

                            <div className="question-list--questions">
                                {preguntas.map(data => (
                                    <Pregunta 
                                        key={data.id}
                                        usuario={data.usuario}
                                        pregunta={data.pregunta} 
                                        setPreguntaId={setPreguntaId}
                                    />
                                ))}
                                
                            </div>

                            {/* <div className="question-list--load-more"><button type="button" className="question-list--load-more-button btn btn-quaternary">Ver más</button></div> */}
                            </>
                        }
                        </>
                    }
                </>
                }
            </div>
        </div>
        </>
    )
}
