import React, {useState, useEffect, useContext, useRef} from 'react';
import { useParams } from "react-router-dom";
import axios from 'axios';

import useLocalStorage from 'hooks/useLocalStorage';
import {TidDataContext} from 'context/tidContext';
import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';

import {API_BASE, VERAPP, getFreeSizeLocalstorage, checkRemainingBytes} from 'components/constants';

import Header from 'components/header/header';

export default function Errores({openMenu, setOpenMenu}) { 

    const { TID } = useParams();

    const {dataTid, sesionTid, setTID} = useContext(TidDataContext);    
    const {dataUsuario} = useContext(UsuarioDataContext);
    const [uuidDevice] = useLocalStorage('uuidDevice');
    const { mostrarAlerta } = useContext(alertaContext);

    const mensajeRef = useRef();

    const [loading, setLoading] = useState(true);   
    const [storageData, setStorageData] = useState({espaciousado:0, espaciolibre:0, total:0, percocup:0});

    const sendAction = async () => {
        const mensaje = mensajeRef.current.value.trim();

        if( mensaje.length < 10 ) {
            mostrarAlerta("Mensaje demasiado corto.", "Reportar problema");
            return;
        }

        if(!uuidDevice){
            console.log("Error uuidDevice in state push");
            return;
        }

        const data = {
            dni: dataUsuario.dni, 
            email: dataUsuario.email,
            nombre: `${dataUsuario.apellidos}, ${dataUsuario.nombres}`,
            TID: parseInt(TID),
            mensaje: mensaje,            
            VERAPP,
            espacio_libre: storageData.espaciolibre,
            idn: sesionTid.idn,
            sesion: sesionTid.sesion,
            avanceNodos: sesionTid.avanceNodos,
            uuidDevice,
        }

        setLoading(true);

        try {
            const result = await axios.post(`${API_BASE}consulta_error.php`, data);
            mostrarAlerta(result.data.text, "Reportar problema");

        }catch (error) {
            mostrarAlerta(error, "Reportar problema");
        }

        setLoading(false);

    }

    useEffect(() => {
        if(dataTid && dataUsuario){
            //setTimeout(() => {
                setLoading(false);
    
                let espaciousado = checkRemainingBytes();            
                let espaciolibre = getFreeSizeLocalstorage();            
                let total = (espaciousado + espaciolibre).toFixed(0);
                let percocup = (espaciousado * 100 / (espaciousado + espaciolibre)).toFixed(1);
    
                espaciousado = espaciousado.toFixed(0);
                espaciolibre = espaciolibre.toFixed(0);
    
                setStorageData({
                    espaciousado, espaciolibre, total, percocup
                })
            //}, 1000);
        }
        // eslint-disable-next-line
    }, [dataTid, dataUsuario]);

    useEffect(() => { 
        setTID(TID);
        // eslint-disable-next-line
    }, []); 
    
    return (
        <>
            <Header setOpenMenu={setOpenMenu} openMenu={openMenu} loading={loading} sendAction={sendAction} titulo={!loading ? 'Reportar problema' : 'Cargando datos...'} />

            { !loading && 
            <div className="section media flex nosombra" style={{paddingLeft:0, paddingRight:0, position:'absolute'}}>   
                <div className="contp f16">
                    <strong>De:</strong> {dataUsuario.email}
                </div>
                <div className="contp bgmgris" onClick={() => mensajeRef.current.focus()}>
                    Describí el problema o compratí tus ideas.
                </div>
                <textarea className="txfull" autoFocus ref={mensajeRef}></textarea>
            </div>
            }
        </>        
    );
}