import React, {useState, useEffect, useContext, useCallback} from 'react';

import { withStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';
import pushContext from 'context/notificacionespush/pushContext';
import useLocalStorage from 'hooks/useLocalStorage';
import {getFreeSizeLocalstorage, checkRemainingBytes } from 'components/constants';

import Header from 'components/header/header';

// import usePushNotifications from 'hooks/usePushNotificationsWoosh';
import SubLoading from 'components/loading';

const IOSSwitch = withStyles((theme) => ({
    root: {
		width: 42,
		height: 26,
		padding: 0,
		margin: theme.spacing(1),
    },
    switchBase: {
      	padding: 1,
      	'&$checked': {
			transform: 'translateX(16px)',
			color: theme.palette.common.white,
			'& + $track': {
				backgroundColor: '#009ddd',
				opacity: 1,
				border: 'none',
			},
      },
      '&$focusVisible $thumb': {
			color: '#009ddd',
			border: '6px solid #fff',
      },
    },
    thumb: {
		width: 24,
		height: 24,
    },
    track: {
		borderRadius: 26 / 2,
		border: `1px solid ${theme.palette.grey[400]}`,
		backgroundColor: theme.palette.grey[50],
		opacity: 1,
		transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  }))(({ classes, ...props }) => {
    return (
		<Switch
			focusVisibleClassName={classes.focusVisible}
			disableRipple
			classes={{
			root: classes.root,
			switchBase: classes.switchBase,
			thumb: classes.thumb,
			track: classes.track,
			checked: classes.checked,
			}}
			{...props}
		/>
    );
});

export default function Ajustes({openMenu, setOpenMenu}) {

    const { mostrarAlerta } = useContext(alertaContext);

    const {
        onClickAskUserPermission,
        userConsent,
        soportaNotificaciones,
        errorsuscribe,
        loadingPush,
        estaSuscripto,
        onClickUnsubscribe,
        onClickSubscribe,
        isReady,
        hwid,
        userId,
        pushToken
    } = useContext(pushContext);

    const isConsentGranted = userConsent === "granted";
        
    const {dataUsuario, logoutAction} = useContext(UsuarioDataContext);

    const [loading, setLoading] = useState(true);
    const [storageData, setStorageData] = useState({espaciousado:0, espaciolibre:0, total:0, percocup:0});
   
    const [tidConfig, setTidConfig] = useLocalStorage('tidConfig', {aumentarTxt:false});
    const [uuidDevice] = useLocalStorage('uuidDevice');

    const [checkAjustes, setCheckAjustes] = useState({
        notificaciones: false,
        utiles: false,
        aumentarTxt: tidConfig.aumentarTxt,
        debug: window._consola ? window._consola.style.display !== "none" : false
    });

    const [notificacionesChk, setnotificacionesChk] = useState(false);

    const handleChange = e => {
        if(e.target.name === 'aumentarTxt'){
            //console.log(e.target.checked)
            setTidConfig({...tidConfig, aumentarTxt:e.target.checked});            
        }
        if(e.target.name === 'notificaciones'){

            if(!e.target.checked && userConsent === 'granted'){
                mostrarAlerta("Está seguro que no desea recibir más notificaciones?, pueden ser importantes para su vida académica.", "Notificaciones", unsubscribeNotificaciones, 'Cancelar Suscripción', 'Lo pensaré');
            }

            if(e.target.checked){
                if(userConsent === 'denied'){
                    mostrarAlerta("Para recibir notificaciones debe desbloquear el consentimiento desde las preferencias de la página en su navegador", "Notificaciones");
                    setnotificacionesChk(false);
                }

                if(userConsent === 'default'){
                    mostrarAlerta("Para recibir notificaciones en este navegador debe aceptar el permiso a continuación", "Notificaciones", setNotificacionesPush, 'Aceptar', null);
                }

                if(userConsent === 'granted'){
                    setNotificacionesPush();
                }
            }


        }else{
            setCheckAjustes({ ...checkAjustes, [e.target.name]: e.target.checked });
        }
    }

    const onShowNotificationPermissionDialog = () => {
        //console.log(userConsent);
        if(userConsent === 'granted' && isReady){
            onClickSubscribe();
        }
    }
    const setNotificacionesPush = () => {
        onClickAskUserPermission(onShowNotificationPermissionDialog);
    }

    const unsubscribeNotificaciones = () => {
        onClickUnsubscribe();
    }

    const logOut = () => {
        const text = '¿Está seguro que desea cerrar la sesión actual?';
        mostrarAlerta(text, 'Cerrar sesión', logoutAction);
    }

    const getallStorage = (dni, all) => {

        var keysOutput = [],
            keys = Object.keys(localStorage),
            i = keys.length;
    
        while ( i-- ) {
            if( keys[i].indexOf(dni) > -1 ){
                if(all){
                    keysOutput.push( keys[i] );
                }else{
                    if(keys[i].indexOf('t') === 0){
                        keysOutput.push( keys[i] );
                    }
                }
            }            
        }
    
        return keysOutput;
    }

    const clearUtilesStorage = () => {
        const keys = getallStorage(dataUsuario.dni, false);
        keys.forEach(item => {
            localStorage.removeItem(item);
        });
        finishInit();
    }

    const clearSesionStorage = () => {
        const keys = getallStorage(dataUsuario.dni, true);
        keys.forEach(item => {
            localStorage.removeItem(item);
        });

        finishInit();
    }

    const clearInfo = e => {
        let text = '';
        switch (e.target.name) {
            case 'lib1':
                text = 'Se va a liberar el espacio usado por utilidades de estudio de todos los TID en este dispositivo.';
                mostrarAlerta(text, 'Eliminar', clearUtilesStorage);
                break;
            case 'lib2':
                text = 'Se va a liberar el espacio usado por la sesión completa.';
                mostrarAlerta(text, 'Eliminar', clearSesionStorage);
                break;
            case 'lib3':
                text = 'Se va a liberar todo el espacio usado por esta aplicación.';
                mostrarAlerta(text, 'Eliminar', ()=>localStorage.clear());
                break;
            default:
                break;
        }
        
    }

    const finishInit = useCallback(() => {

        console.log('finishInit');
        
        setCheckAjustes({...checkAjustes, aumentarTxt:tidConfig.aumentarTxt });

        //setTimeout(() => {                

            let espaciousado = checkRemainingBytes();            
            let espaciolibre = getFreeSizeLocalstorage();            
            let total = (espaciousado + espaciolibre).toFixed(0);
            let percocup = (espaciousado * 100 / (espaciousado + espaciolibre)).toFixed(1);

            espaciousado = espaciousado.toFixed(0);
            espaciolibre = espaciolibre.toFixed(0);

            setStorageData({
                espaciousado, espaciolibre, total, percocup
            })

            setLoading(false);

        //}, 500);
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        if(dataUsuario){

            if ("Notification" in window) {
                
                if(isReady){
                    finishInit();
                }
            }else{
                console.log("Este navegador no es compatible con las notificaciones de escritorio");
                finishInit();
            }
        }
        // eslint-disable-next-line
    }, [dataUsuario, isReady]);
   
    useEffect(() => {
        const isCheck = soportaNotificaciones && isConsentGranted && estaSuscripto;
        if(notificacionesChk !== isCheck){
            setnotificacionesChk(isCheck);
        }
    }, [soportaNotificaciones, isConsentGranted, estaSuscripto, notificacionesChk]);

    useEffect(() => {
        if(loadingPush){
            console.log(loadingPush)
        }else if(!loadingPush && !errorsuscribe){
            if(userConsent !== 'granted' || !estaSuscripto){
                setnotificacionesChk(false);
            }
            setnotificacionesChk(soportaNotificaciones && isConsentGranted && estaSuscripto);
        }        
        // eslint-disable-next-line
    }, [loadingPush, userConsent, errorsuscribe])

    
    return (
        <>
            <Header setOpenMenu={setOpenMenu} openMenu={openMenu} loading={loading} titulo={!loading ? 'Ajustes' : 'Cargando datos...'} />

            { (!loading && dataUsuario) &&
            <div className="section flex nosombra" style={{paddingLeft:0, paddingRight:0}}> 
                <br />
                <FormControlLabel
                    className="ajustes"
                    control={
                        <IOSSwitch
                            checked={notificacionesChk}
                            onChange={handleChange}
                            name="notificaciones"
                        />
                    }
                    label={soportaNotificaciones ? <>{loadingPush && <SubLoading />}Permitir notificaciones</> : <>{loadingPush && <SubLoading />}Este dispositivo no permite notificaciones</>}
                    disabled={!soportaNotificaciones || loadingPush}
                />
                {errorsuscribe && <p className="error">{errorsuscribe.message}</p>}
                {(soportaNotificaciones && notificacionesChk) && 
                    <div className="ajustes"><table><tbody><tr>
                    <td><p className="f11 wfijo85" style={{whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis'}}>
                        hwid: {hwid}<br/> 
                        userId: {userId}<br />
                        pushToken: {pushToken}
                    </p></td></tr></tbody></table></div>
                }

                {/* <FormControlLabel
                    className="ajustes"
                    control={
                        <IOSSwitch
                            checked={checkAjustes.utiles}
                            onChange={handleChange}
                            name="utiles"
                        />
                    }
                    label={`Mostrar siempre los botones de marcado y notas en el texto`}
                /> */}

                <div className="ajustes">
                <p className="f11 wfijo85" style={{whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis'}}>uitid: {uuidDevice}</p>
                </div>

                <hr style={{width:'100%'}} />
                <div className="ajustes">
                <p><strong>Visualización de contenido</strong></p>

                </div>
                <FormControlLabel
                    className="ajustes"
                    control={
                        <IOSSwitch
                            checked={checkAjustes.aumentarTxt}
                            onChange={handleChange}
                            name="aumentarTxt"
                        />
                    }
                    label="Aumentar tamaño de texto"
                />
                

                <hr style={{width:'100%'}} />

                <div className="ajustes">
                    <table><tbody><tr><td>

                    <p><strong>Espacio de almacenamiento</strong><span className="conteesp"><span id="espacio" style={{width:`${storageData.percocup < 1 ? 1 : storageData.percocup}%`}}></span></span>
                            <span className="f10">{storageData.espaciousado} Kb en uso y {storageData.espaciolibre} Kb disponibles.</span></p>

                    <p className="f11">El espacio ocupado por la sesión completa, y el espacio libre.</p></td></tr></tbody></table>


                    {/* <table><tbody><tr>
                        <td><p>Liberar espacio usado por anotaciones y resúmenes en este dispositivo</p></td>
                    <td width="30"></td>
                    <td align="right" valign="top">
                        <button className="gRojo" name="lib1" onClick={clearInfo}>Liberar espacio</button>
                    </td></tr></tbody></table> */}

                    <table><tbody><tr>
                        <td><p>Liberar todo el espacio usado por la sesión completa en este dispositivo</p></td>
                    <td width="30"></td>
                    <td align="right" valign="top">
                        <button className="gRojo" name="lib2" onClick={clearInfo}>Liberar todo</button>
                    </td></tr></tbody></table>

                    { dataUsuario.debug &&
                        <table><tbody><tr>
                        <td><p>Liberar TODO el espacio</p></td>
                        <td width="30"></td>
                        <td align="right" valign="top">
                        <button className="gRojo" name="lib3" onClick={clearInfo}>Eliminar todo</button>
                        </td></tr></tbody></table>
                    }
                
                </div>

                { dataUsuario.debug &&
                    <>
                        <hr style={{width:'100%'}} />
                        <FormControlLabel
                            className="ajustes"
                            control={
                                <IOSSwitch
                                    checked={checkAjustes.debug}
                                    onChange={handleChange}
                                    name="debug"
                                />
                            }
                            label={`Mostrar cónsola. Para control interno.`}
                        />
                    </>
                }



                <hr style={{width:'100%'}} />

                <div className="ajustes">
                    <table><tbody><tr><td>
                        <p>Cerrar sesión de <strong>{dataUsuario.nombres}, {dataUsuario.apellidos}</strong></p>
                        <p className="f11">Se eliminarán los datos del navegador</p></td>
                    <td width="30"></td>
                    <td align="right" valign="top">
                        <button className="gRojo" onClick={logOut} >Cerrar sesión</button>
                    </td></tr></tbody></table>
                </div>
                
            </div>
            }
        </>        
    );
}