import React, {useState, useEffect, useContext} from 'react';
import { useHistory, useLocation } from "react-router-dom";

import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';
import {VERAPP, getRandomString, device_data} from 'components/constants';

import logo from 'img/login-logo.svg';
import SubLoading from 'components/loading';

export default function Login({iesAuth}) {

    let history = useHistory();
    let location = useLocation();

    const {loginAction} = useContext(UsuarioDataContext);
    const { mostrarAlerta } = useContext(alertaContext);

    const [loading, setLoading] = useState(true);

    const namesForm = {
        'dni': getRandomString(15),
        'pass': getRandomString(15)
    }
    const reverseNamesForm = {};
    Object.keys(namesForm).forEach(key => reverseNamesForm[namesForm[key]] = key);

    const [form, setForm] = useState({dni:'', pass:''});

    let { from } = location.state || { from: { pathname: "/" } };

    const isTid = from.pathname.indexOf('/tid') > -1;


    let login = async e => {
        e.preventDefault();

        const dni = form.dni.trim().replace(/\.+/g, '');
        const pass = form.pass.trim();

        if( dni.length < 5 ){
            mostrarAlerta("Escriba su dni", "Login");
            return;
        }
        if(pass.length < 3){
            mostrarAlerta("Escriba una contraseña válida", "Login");
            return;
        }

        setLoading(true);

        const data = { dni, pass, VERAPP, device_data };

        const res = await loginAction(data);

        if(res){
            iesAuth.authenticate(() => {
                if(!isTid) from = '/';
                history.replace(from);
            });
        }
        setLoading(false);
    }

    const handleChange = e => {
        const nameField = reverseNamesForm[e.target.name];
        setForm({...form, [nameField]:e.target.value});
    }

    useEffect( () => {
        if(loading){
            setLoading(false);
        }
        // eslint-disable-next-line
    }, [])

    return (
        <React.Fragment>

            <div className="contenedor login">
                <br />
                <img src={logo} alt="" />
                <br />

                <br /><br />

                <form onSubmit={login}>

                    <input type="text" pattern="[0-9]*" onChange={handleChange} name={namesForm.dni} placeholder=" dni " tabIndex="1" autoFocus style={{padding: 0}} />

                    <input type="password" onChange={handleChange} name={namesForm.pass} placeholder=" Contraseña " tabIndex="2" style={{padding: 0}} />

                    <br /><br />

                    <button type="submit" tabIndex="3" disabled={loading} >{loading && <SubLoading />} LOGIN</button>
                </form>
            </div>

        </React.Fragment>
    )
}