import React from 'react';
import { useParams, Link } from "react-router-dom";

import PajaroImg from 'img/pajaro.png';

export default function PERROR() {

    const {origen} = useParams();

    const orgDecode = decodeURIComponent(origen);

    return (
        <div className="seccion" id="seccion_cont">
            <div className="contp">
                <table width="100%">
                    <tbody>
                    <tr>
                        <td>
                            <br /><p className="thome">Oh!<br />
                            Algo salió mal, revise su conexión.</p>                                            
                        </td>
                        <td width="138">
                            <img src={PajaroImg} alt="" />
                        </td>
                    </tr>
                    </tbody>
                </table>
                

            </div>
            <div className="contp">
                <p><Link to={'/'} >Ir al home</Link></p>
                <p><a href={orgDecode}>Cargar nuevamente la página</a></p>
            </div>
        </div>
    )
}