import React, { createContext, useState, useEffect, useContext } from 'react';
import axios from 'axios';
import {API_BASE, VERAPP, device_data} from 'components/constants';
import getUuidDevice from 'components/uuidevice';
import alertaContext from './alertas/alertaContext';
import pushContext from './notificacionespush/pushContext';
import useLocalStorage from 'hooks/useLocalStorage';

import { useSnackbar } from 'notistack';

export const UsuarioDataContext = createContext();

export default function UsuarioDataProvider ({children, iesAuth, setDni}) {

    const { enqueueSnackbar } = useSnackbar();

    const { mostrarAlerta } = useContext(alertaContext);
    const { initPush, isReady, hwid, onClickUnsubscribe } = useContext(pushContext);

    const [usuarioLog, setUsuarioLog] = useLocalStorage('usuarioLog');
    const [uuidDevice, setUuidDevice] = useLocalStorage('uuidDevice');

    const [dataUsuario, setDataUsuario] = useState({});
    const [remoteConstants, setRemoteConstants] = useState({});

    const logoutAction = async () => {

        // actualiza bd para no recibir notificaciones y desiscribe pw
        if(hwid){
            onClickUnsubscribe();            
        }
        const data = {
            dni:usuarioLog.dni,
            uuidDevice: uuidDevice,
        }
        console.log(data);
        try {
            const result = await axios.post(`${API_BASE}logout.php`, data);
            console.log(result);
        }catch (error) {
            console.log(error);
        }
        setUsuarioLog(null);
        iesAuth.signout();
        setPwLoaded(false);
        setDataUsuario(null);
        window.location.replace("/");
    }

    const showWelcome = txt => {
        setTimeout(() => {
            enqueueSnackbar(txt);
        }, 1000);
    }

    const loginAction = async data => {

        console.log(uuidDevice);
        
        if(uuidDevice && uuidDevice !== ''){
            data.uuidDevice = uuidDevice;
        }else{
            //const uuid = (Date.now().toString(36) + Math.random().toString(36).substr(2, 5))+(Date.now().toString(36) + Math.random().toString(36).substr(2, 5))+(Date.now().toString(36) + Math.random().toString(36).substr(2, 5));

            const uuid = getUuidDevice();
            setUuidDevice(uuid);
            data.uuidDevice = uuid;
        }
        try {
            const result = await axios.post(`${API_BASE}login.php`, data);

            console.log(result);

            if(result.data.r === 0){
                mostrarAlerta(result.data.text, "Login", logoutAction, 'Aceptar', null);
                return false;
            }
            //40572907
            //console.log(result.data.usuario)

            /*
            Ver tiempo q hace q no entra y darle bienvenida y decirle q fue el ultimo tid q vio y en que nodo
            */
            const {dni, VERAPP, token} = result.data.usuario;

            setRemoteConstants({
                API_IES:result.data.API_IES
            });
            
            setUsuarioLog({dni, token, VERAPP});
            setDataUsuario(result.data.usuario);

            showWelcome(result.data.text);
            
            return true;

        }catch (error) {
            mostrarAlerta(error, "Error login", logoutAction, 'Aceptar', null);
            return false;
        }
    }    

    const [pwLoaded, setPwLoaded] = useState(false);
    const [readyPW, setreadyPW] = useState(false);

    const getNotDisplayMsg = async () => {

        const Pushwoosh = window.Pushwoosh;

        try{
            const existingSubscription = await Pushwoosh.isSubscribed();

            if(existingSubscription){
                const opened = await Pushwoosh.pwinbox.messagesWithNoActionPerformedCount();
                console.log(`${opened} messages opened`);

                const unread = await Pushwoosh.pwinbox.unreadMessagesCount();
                console.log(`${unread} messages unread`);

                const totalmessages = await Pushwoosh.pwinbox.messagesCount();
                console.log(`${totalmessages} messages`);

                const messages = await Pushwoosh.pwinbox.loadMessages();

                if(totalmessages > 0){                
                    console.log(messages);
                }
        
            }
        }catch (e){
            console.log(e);
        }
    }

    const loadPw = () => {
        if(!pwLoaded) {
            initPush(dataUsuario.dni);
            setPwLoaded(true);
        }
    }

    useEffect( () => {
        if(isReady){
            setreadyPW( true);
            getNotDisplayMsg();
        }
    }, [isReady])

    useEffect( () => {
        if( dataUsuario && dataUsuario.dni ){
            setDni(dataUsuario.dni);   
            loadPw();
        }
        // eslint-disable-next-line
    }, [dataUsuario, setDni]);

    useEffect( () => {
        if(iesAuth && usuarioLog){
            
            const data = { dni:usuarioLog.dni, token:usuarioLog.token, VERAPP, device_data };

            loginAction(data);
        }
        // eslint-disable-next-line
    }, [])

 
    return (
        <React.Fragment>
            <UsuarioDataContext.Provider value={{dataUsuario, readyPW, remoteConstants, logoutAction, loginAction}}>            
                {children}
            </UsuarioDataContext.Provider>
        </React.Fragment>
    )
}
